<script>
import Konva from "konva";
const width = window.innerWidth;
const height = window.innerHeight;

const generateTargets = () => {
  const images = [];
  for (let i = 0; i < 7; i++) {
    images.push({
      id: i,
      x: i < 5 ? width * 0.1 : width * 0.7,
      y: i < 5 ? height * 0.2 * i : height * 0.3 * (i - 4),
      image: "",
      width: width / 9,
      height: height / 5,
    });
  }
  return images;
};
export default {
  created() {
    for (let i = 0; i < 7; i++) {
      let image = new window.Image();
      image.src = require(`@/assets/images/activities/10_${8 + i}.png`);
      image.onload = () => {
        this.targets[i].image = image;
      };
    }
  },
  data() {
    return{
      questions:[
        "¿De qué país es el Legoland de la imagen?",
        "¿Qué piezas de tu kit reconoces en la construcción de Legoland?",
        "¿Qué sentido utilizaste para reconocer las piezas?",
        "¿Qué otros sentidos posees?",
        "¿Qué hace un sensor de movimiento?",
        "¿Qué haz programado en el bloque de Pantalla para mostrar?",
        "¿Qué hace el sensor de inclinación?",
        "¿Qué bloques haz utilizado para programar el sensor de inclinación?",
        "¿Cómo funciona este programa?",
        
      ],
      answers:[
        "","","","",
        ["Vista","",""],
        ["","Nariz",""],     
        ["","","Sabores"],     
        ["Tacto","",""],     
        ["","Oídos",""],
        "","",
        "","","",
        {src:"",file:""},
        ["Hacia arriba",""],
        ["Hacia la izquierda",""],
        ["Hacia la derecha",""],
        ["Cualquier inclinación",""],
        ["Sin inclinación",""],
        ["Hacia abajo",""],
        ["¿El sensor de inclinación se activa cuando se coloca totalmente vertical?",""],
        ["¿El sensor puede detectar una inclinación de 45°?",""],
        ["¿Existe la posibilidad de detectar hasta 6 posiciones con este sensor?",""],
        ["¿El sensor puede funcionar sin ser conectado al Hub USB?",""],
        "","","",""
      ],
      photoPlaceholder: require("@/assets/images/activities-assets/activity_2-2/Recurso 214@4x.png"),
      stageSize: {
        width: width - 800,
        height: height - 300,
      },
      changeLineStartingPoint: [],
      changeLine: false,
      targets: generateTargets(),
      connections: [],
      drawningLine: false,
    }
  },
  methods:{
    isInput(i,j){
      if(
        (i == 2 && j == 1) ||
        (i == 3 && j == 2) ||
        (i == 4 && j == 3) ||
        (i == 2 && j == 4) ||
        (i == 3 && j == 5)
      ){
        return false;
      }else{
        return true;
      }
    },
    getItem(i,j){
      if(i == 2 && j == 1){
        return this.answers[4][0];
      }else if(i == 3 && j ==2){
        return this.answers[5][1];
      }else if(i == 4 && j == 3){
        return this.answers[6][2];
      }else if(i == 2 && j == 4){
        return this.answers[7][0];
      }else if(i == 3 && j == 5){
        return this.answers[8][1];
      }
    },
    handleMouseDown(e) {

      const onCircle = e.target instanceof Konva.Image;
      if (!onCircle || e.target.id() >= 5) {
        return;
      }
      if (e.target.id() < 5 && this.drawningLine) {
        this.connections.pop();
      }
      this.drawningLine = true;
      this.changeLine = false;
      this.connections.forEach((point) => {
        if (
          point.points[0] == e.target.x() + e.target.width() / 2 &&
          point.points[1] == e.target.y() + e.target.height() / 2
        ) {
          this.changeLine = true;
          this.changeLineStartingPoint = point;
          //   console.log("Changing =>", this.changeLineStartingPoint);
        }
      });
      this.connections.push({
        id: Date.now(),
        points: [
          e.target.x() + e.target.width() / 2,
          e.target.y() + e.target.height() / 2,
        ],
      });
    },
    handleMouseMove(e) {
      if (!this.drawningLine) {
        return;
      }
      const pos = e.target.getStage().getPointerPosition();
      const lastLine = this.connections[this.connections.length - 1];
      lastLine.points = [lastLine.points[0], lastLine.points[1], pos.x, pos.y];
    },
    handleMouseUp(e) {
      //   console.log(e.target);
      const onCircle = e.target instanceof Konva.Image;
      if (!onCircle) {
        return;
      }
      if (e.target.id() < 5 && this.drawningLine) {
        this.connections.pop();
        this.drawningLine = false;
        return;
      }
      this.drawningLine = false;
      let lastLine;
      if (this.changeLine) {
        lastLine = this.changeLineStartingPoint;
        this.connections.pop();
      } else {
        lastLine = this.connections[this.connections.length - 1];
      }
      lastLine.points = [
        lastLine.points[0],
        lastLine.points[1],
        e.target.x() + e.target.width() / 2,
        e.target.y() + e.target.height() / 2,
      ];
    },
    uploadImage(e) {
      const file = e.target.files[0];
      const reader = new FileReader();
      this.answers[14].file = file;
      reader.onload = (e) => {
        this.answers[14].src = e.target.result;
      };
      reader.readAsDataURL(file);
    },
  },
   mounted() {
    const canvas = document.querySelector(".canvas");
    const observer = new ResizeObserver(() => {
      this.stageSize.width = canvas.offsetWidth;
      this.stageSize.height = canvas.offsetHeight;
      this.targets.forEach((img, index) => {
        this.connections.forEach((point) => {
          if (
            point.points[0] == img.x + img.width / 2 ||
            point.points[1] == img.y + img.height / 2
          ) {
            point.points[0] = this.stageSize.width * 0.1 + img.width / 2;
            point.points[1] =
              this.stageSize.height * 0.15 * index + img.height / 2;
          }
          if (
            point.points[2] == img.x + img.width / 2 ||
            point.points[3] == img.y + img.height / 2
          ) {
            point.points[2] = this.stageSize.width * 0.7 + img.width / 2;
            point.points[3] =
              this.stageSize.height * 0.2 * (index - 4) + img.height / 2;
          }
        });
        if (this.stageSize.width <= 768) {
          img.width = this.stageSize.width / 4;
          img.height = this.stageSize.height / 5;
        }
        img.x =
          index < 5 ? this.stageSize.width * 0.1 : this.stageSize.width * 0.7;
        img.y =
          index < 5
            ? this.stageSize.height * 0.2 * index
            : this.stageSize.height * 0.3 * (index - 4);
      });
    });
    observer.observe(canvas);
  },
}
</script>

